import React, { useState, useEffect } from 'react';
import Card from '../elements/Card';

export default function CardList () {
  const [data, setdata] = useState([]);

  useEffect(() => {
    FetchData();
  }, []);

  const FetchData = async () => {
    const res = await fetch('https://backend-lobrockyl.herokuapp.com');
    const result = await res.json();
    if (result.success) setdata(result.images);
  };

  return (
    <div className='container my-12 mx-auto px-4 md:px-12'>
      <div className='flex flex-wrap justify-center mx-1 lg:mx-4'>
        {data.length > 0 &&
          data.map((value) => {
            return (
              <Card
                key={value._id}
                name={value.ImgName}
                image={value.ImgURL}
                detail={value.ImgDetails}
              />
            );
          })}
          {data.length <= 0 && <div className='flex justify-center w-full text-gray-500 text-2xl font-bold'>No data Found!</div>}
      </div>
    </div>
  );
}
